"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { Label } from "~/components/ui/label";
import { Textarea } from "~/components/ui/textarea";
import { toast } from "~/hooks/use-toast";

export default function NewTeam() {
    const router = useRouter();
    const [loading, setLoading] = useState(false);

    async function onSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        setLoading(true);

        try {
            const formData = new FormData(e.currentTarget);
            const teamName = formData.get("teamName") as string;
            const playerEmails = formData.get("playerEmails") as string;

            // Split emails by commas or newlines and trim whitespace
            const emails = playerEmails
                .split(/[,\n]/)
                .map(email => email.trim())
                .filter(Boolean);

            const response = await fetch("/api/teams", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    name: teamName,
                    playerEmails: emails,
                }),
            });

            if (!response.ok) {
                throw new Error("Failed to create team");
            }

            const data = await response.json() as { teamCode: string };
            toast({
                title: "Success",
                description: `Team created! Share this code with the team: ${data.teamCode}`,
            });
            router.push("/admin/teams");
            router.refresh();
        } catch (error) {
            toast({
                variant: "destructive",
                title: "Error",
                description: "Failed to create team. Please try again.",
            });
            console.error("Error creating team:", error);
        } finally {
            setLoading(false);
        }
    }

    return (
        <div className="max-w-2xl">
            <div className="mb-8">
                <h1 className="text-3xl font-bold tracking-tight">Create New Team</h1>
                <p className="text-slate-400">Create a new team and invite players.</p>
            </div>

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle>Team Details</CardTitle>
                </CardHeader>
                <CardContent>
                    <form onSubmit={onSubmit} className="space-y-6">
                        <div className="space-y-2">
                            <Label htmlFor="teamName">Team Name</Label>
                            <Input
                                id="teamName"
                                name="teamName"
                                placeholder="Enter team name"
                                required
                                className="bg-slate-950"
                            />
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="playerEmails">
                                Player Emails
                                <span className="ml-1 text-sm text-slate-400">
                                    (one per line or comma-separated)
                                </span>
                            </Label>
                            <Textarea
                                id="playerEmails"
                                name="playerEmails"
                                placeholder="player1@example.com&#10;player2@example.com"
                                required
                                className="min-h-[120px] bg-slate-950"
                            />
                        </div>

                        <div className="flex justify-end gap-4">
                            <Button
                                type="button"
                                variant="outline"
                                onClick={() => router.back()}
                                disabled={loading}
                            >
                                Cancel
                            </Button>
                            <Button type="submit" disabled={loading}>
                                {loading ? "Creating..." : "Create Team"}
                            </Button>
                        </div>
                    </form>
                </CardContent>
            </Card>
        </div>
    );
} 